* {
  padding: 0;
  margin : 0;
  box-sizing: border-box;

}

.content{
  height: 80vh;
}

.navbar{
  background-color: rgb(140, 140, 162) !important;
}

.register-page{
  display:flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}

.registerorlogin-link{
  margin-right: 10px; /* Adjust the margin as needed */
}


/*transacction page */
.filters{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px gray;

}


/*loginpage*/
.login-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: rgb(84, 146, 168);
}

.login-container {
  position: relative;
  width: 100%;
  max-width: 600px; /* Adjust as needed */
}

.login-image {
  width: 100%;
  height: auto;
  border-radius: 8px; /* Adjust as needed */
}

.login-form-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity:0.95;
}

.login-form {
  width: 80%;
  max-width: 400px; /* Adjust as needed */
  background-color: white;
  padding: 20px;
  border-radius: 8px; /* Adjust as needed */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); /* Adjust as needed */
}

